<template>
  <div>
    <!--  -->
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-button type="primary" @click="addDialogVisible = true">
        
      </el-button>

      <!--  -->
      <el-table border stripe :data="rolesList">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!-- {{ scope.row.children }} -->
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <el-col :span="5">
                <!--  -->
                <el-tag closable @close="removeRightById(scope.row, item1.id)">
                  {{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <!--  -->
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                    >
                      {{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!--  -->
                    <el-tag
                      :class="[i3 === 0 ? '' : 'bdtop']"
                      v-for="(item3, i3) in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                    >
                      {{ item3.authName }}</el-tag
                    >
                    <!-- <i class="el-icon-caret-right"></i >-->
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="" prop="roleName"> </el-table-column>
        <el-table-column label="" prop="roleDesc"> </el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            >
              
            </el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeById(scope.row.id)"
            >
              
            </el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetDialogVisible(scope.row)"
            >
              
            </el-button>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      width="50%"
      :visible.sync="addDialogVisible"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        label-width="80px"
        ref="addFormRef"
        :rules="editFormRules"
        :model="addForm"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole">   </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      width="50%"
      ref="editFormRef"
      :visible.sync="editDialogVisible"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        label-width="80px"
        ref="editFormRef"
        :rules="editFormRules"
        :model="editForm"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInfo">   </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      width="50%"
      ref="editFormRef"
      :visible.sync="setDialogVisible"
    >
      <!--  -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="rightKeyList"
        ref="treeRef"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false"> </el-button>
        <el-button type="primary" @click="setRight">   </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolesList: [],
      rightsList: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      rightKeyList: [],

      // 
      addDialogVisible: false,
      editDialogVisible: false,
      setDialogVisible: false,

      // 
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      editForm: {
        roleName: '',
        roleDesc: ''
      },
      roleId: '',
      // 
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
        ]
      },
      editFormRules2: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    async getRolesList() {
      // 
      const { data: res } = await this.$http.get('roles')
      // 
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.rolesList = res.data
      this.$message.success('')
      //   console.log(this.rolesList) // 
    },

    // 
    addRole() {
      this.$refs.addFormRef.validate(async (valid) => {
        // 
        if (!valid) {
          return this.$message.error('')
        }

        // 
        const { data: res } = await this.$http.post('roles', this.addForm)

        // 
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        // 
        this.$message.success(res.meta.msg)
        this.addDialogVisible = false // 
        this.getRolesList()
      })
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.editForm = res.data
      this.editDialogVisible = true
    },

    // 
    editInfo() {
      // 

      this.$refs.editFormRef.validate(async (valid) => {
        // 
        if (!valid) {
          return this.$message.error('')
        }
        // 
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          this.editForm
        )
        // 
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getRolesList()
        // 
        this.$message.success(res.meta.msg)
      })
    },

    // 
    async removeById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      //  confirm
      //   console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.$message.success(res.meta.msg)

      // 
      this.getRolesList()
    },
    // 
    async removeRightById(role, id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      //  confirm
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        'roles/' + role.id + '/rights/' + id
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success(res.meta.msg)

      // 
      role.children = res.data
    },

    // 
    async showSetDialogVisible(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.rightsList = res.data
      this.setDialogVisible = true
      //   console.log(role)
      const idList = this.getIdList(role, [])
      //   console.log(idList)
      this.rightKeyList = idList
      //   this.rightsList = idList
      //   console.log(this.rightsList)
    },

    // id
    getIdList(obj, idList) {
      if (obj.children === undefined) {
        // console.log(obj.id)
        return idList.push(obj.id)
      } else {
        obj.children.forEach((element) => {
          this.getIdList(element, idList)
        })
      }
      return idList
    },

    // 
    async setRight() {
      const keyList = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const keyStr = keyList.join(',')
      const { data: res } = await this.$http.post(
        'roles/' + this.roleId + '/rights',
        {
          rids: keyStr
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.setDialogVisible = false
    }
  },
  created() {
    // 
    this.getRolesList()
  }
}
</script>

<style scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
