<template>
  <!--  -->
  <div>
    <!-- <h1>users</h1> -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- <div v-for="o in 4" :key="o" class="text item">
        {{ ' ' + o }}
      </div> -->
      <el-row :gutter="20">
        <el-col :span="9"
          ><el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">
            </el-button
          >
        </el-col>
      </el-row>
      <el-table :data="userList" border stripe>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch
              @change="userStateChaneged(scope.row)"
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
        <el-table-column label="">
          <!-- <template slot-scope="scope"> -->
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserById(scope.row.id)"
            ></el-button>

            <el-tooltip
              effect="dark"
              content=""
              :enterable="false"
              placement="top"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoleDialog(scope.row)"
              >
              </el-button>
            </el-tooltip>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 4, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- <span></span> -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <!-- <el-button type="primary" @click="addDialogVisible = false">
           
        </el-button> -->
        <el-button type="primary" @click="addUser">   </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      ref="editFormRef"
      width="50%"
      :visible.sync="editDialogVisible"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        label-width="70px"
        :rules="editFormRules"
        ref="editFormRef"
      >
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <!-- <el-button type="primary" @click="addDialogVisible = false">
           
        </el-button> -->
        <el-button type="primary" @click="editUserInfo">   </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" width="50%" :visible.sync="setDialogVisible">
      <div>
        <p>: {{ roleInfo.username }}</p>
        <p>: {{ roleInfo.role_name }}</p>
        <p>
          :
          <el-select v-model="selectRoleId" placeholder="">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false"> </el-button>
        <el-button type="primary" @click="setRole">   </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      //   const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      const regEmail = /^\w+@\w+(\.\w+)+$/ // 

      if (regEmail.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }
    var checkPhone = (rule, value, cb) => {
      //   const regPhone = /^(0|86|17951)?/
      const regPhone = /^1[34578]\d{9}$/

      if (regPhone.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }

    return {
      // 
      selectRoleId: '',
      setDialogVisible: false,
      roleList: [],

      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 3
      },
      userList: [],
      total: 3,
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      roleInfo: {},
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
          //   { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }

          //   { min: 11, max: 11, message: '', trigger: 'blur' }
        ]
      },
      // 

      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
          //   { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }

          //   { min: 11, max: 11, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      //   const strl = await this.$http.get('users')
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      //   console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.userList = res.data.users
      this.total = res.data.total
      //   console.log(strl)
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    async userStateChaneged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.addDialogVisible = false
        this.getUserList()
      })
    },
    async showEditDialog(id) {
      //   console.log(`id:${id}`)
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data

      this.editDialogVisible = true
    },
    // 
    editUserInfo() {
      // 
      this.$refs.editFormRef.validate(async (valid) => {
        // 
        if (!valid) {
          return this.$message.error('')
        }
        // 
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        // 
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')

        // console.log(res)
      })
    },
    // 
    async removeUserById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }
      ).catch((err) => {
        //  
        // cancel
        // this.$message.info('')
        return err
      })
      //  confirm
      //   console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete('users/' + id, { id: id })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.$message.success('')
      // 
      this.getUserList()
    },
    // 
    async setRoleDialog(role) {
      this.roleInfo = role
      // 
      const { data: res } = await this.$http.get('roles/')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success(res.meta.msg)
      this.roleList = res.data

      this.setDialogVisible = true
    },
    async setRole() {
      // console.log(this.selectRoleId)
      if (this.selectRoleId === '') {
        return this.$message.error('')
      }
      // console.log(this.roleInfo)
      // this.roleInfo.role_name = this.selectRoleId
      // console.log(this.username)
      console.log(this.selectRoleId)
      const { data: res } = await this.$http.put(
        'users/' + this.roleInfo.id + '/role',
        {
          rid: this.selectRoleId
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.selectRoleId = []
      this.getUserList()
      this.$message.success(res.meta.msg)
      this.setDialogVisible = false
    }
  }
}
</script>

<style scoped>
</style>
