<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="openAddCate"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        max-height="5"
      >
        <!--isOk  -->
        <template slot="isOk" slot-scope="scope">
          <i
            v-if="scope.row.cat_deleted === false"
            class="el-icon-success"
            style="color: lightgreen"
          ></i>
          <i
            v-if="scope.row.cat_deleted === true"
            class="el-icon-error"
            style="color: red"
          ></i>
        </template>
        <!-- order -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-if="scope.row.cat_level === 2"></el-tag>
        </template>
        <!-- opt -->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="openEditCate(scope.row)"
          >
            
          </el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="del_Cate(scope.row)"
          >
            
          </el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        :page-sizes="[3, 5, 10, 15]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="this.total"
        :page-size="this.queryInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        @prev-click="prevClick"
        @next-click="nextClick"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      width="50%"
      :visible.sync="showAddCateDialog"
      @close="CloseAddForm"
    >
      <!--  -->
      <el-form
        label-width="80px"
        :model="addForm"
        :rules="addFormRule"
        ref="addFormRef"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            :options="ParentCateList"
            :props="cascaderProps"
            @change="ParentCateChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddCateDialog = false"> </el-button>

        <el-button type="primary" @click="PostAddForm">   </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title=""
      width="50%"
      :visible.sync="showEditCateDialog"
      @close="CloseEditForm"
    >
      <!--  -->
      <el-form
        label-width="80px"
        :model="EditForm"
        :rules="EditFormRule"
        ref="EditFormRef"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="EditForm.cat_name"> </el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="showEditCateDialog = false"> </el-button>
        <el-button type="primary" @click="PostEditForm">   </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      // #region

      // #endregion

      // 
      cateList: [],
      ParentCateList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // columns
      columns: [
        { label: '', prop: 'cat_name' },
        { label: '', type: 'template', template: 'isOk' },
        { label: '', type: 'template', template: 'order' },
        { label: '', type: 'template', template: 'opt' }
      ],
      // 
      showAddCateDialog: false,

      // 
      addFormRule: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 1,
            max: 100,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ]
      },
      addForm: {
        cat_pid: 0,

        cat_name: '',
        cat_level: 0
      },
      cascaderProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover',
        checkStrictly: true
      },
      selectKey: [],
      // 
      showEditCateDialog: false,
      EditForm: {
        cat_name: '',
        cat_id: 0
      },
      EditFormRule: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 1,
            max: 10,
            message: ' 1  10 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getCateList()
    // console.log('musaly')
  },
  methods: {
    // 
    async getCateList() {
      // 
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      // 
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data[1]) // 
      // console.log(res)
      // 
      this.$message.success('')
      this.cateList = res.data.result
      this.total = res.data.total
    },
    // 
    async handleSizeChange(value) {
      this.queryInfo.pagesize = value
      this.getCateList()
    },
    async handleCurrentChange(value) {
      this.queryInfo.pagenum = value
      this.getCateList()
    },
    async prevClick(value) {
      this.queryInfo.pagenum = value - 1
      this.getCateList()
    },
    async nextClick(value) {
      this.queryInfo.pagenum = value + 1
      this.getCateList()
    },

    // 
    // #region
    openAddCate() {
      this.getAddCateList()
      this.showAddCateDialog = true
    },
    async getAddCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      // 
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ParentCateList = res.data
    },
    ParentCateChange() {
      console.log(this.selectKey)
      if (this.selectKey.length > 0) {
        this.addForm.cat_pid = this.selectKey[this.selectKey.length - 1]
        this.addForm.cat_level = this.selectKey.length
        return 0
      } else {
        this.addForm.cat_pid = 0
        this.addForm.cat_level = 0
      }
      console.log(this.addForm)
    },
    CloseAddForm() {
      this.$refs.addFormRef.resetFields()
      this.selectKey = []
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    },
    PostAddForm() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(this.addForm)
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post('categories', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')

        this.getCateList()
        this.showAddCateDialog = false
      })
    },
    // #endregion

    // 
    // #region
    openEditCate(target) {
      this.EditForm.cat_name = target.cat_name
      this.EditForm.cat_id = target.cat_id
      this.showEditCateDialog = true
    },
    async PostEditForm() {
      const { data: res } = await this.$http.put(
        'categories/' + this.EditForm.cat_id,
        {
          cat_name: this.EditForm.cat_name
        }
      )
      // 
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getCateList() // 
      this.$message.success(res.meta.msg)
      this.showEditCateDialog = false
    },
    CloseEditForm() {
      this.$refs.EditFormRef.resetFields()
    },
    // #endregion

    // 
    // #region
    async del_Cate(target) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          cancelButtonText: '',
          confirmButtonText: '',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        'categories/' + target.cat_id
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success(res.meta.msg)
      this.getCateList() // 
    }

    // #endregion
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
